.banner {
  color: white;
  object-fit: contain;
  height: 468px;
}
.banner_contents {
  margin-left: 20px;
  padding-top: 140px;
  height: 190px;
}
.banner_title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.banner_description {
  width: 45rem;
  /* max-width: 360px; */
  line-height: 1.3;
  padding-top: 1rem;
  font-style: 0.8rem;
  max-width: 360px;
  height: 80px;
}
.banner_button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;
}
.banner_button:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}
.banner--fadeBottom {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61) #111
  );
}

/* media queries */
@media (max-width: 768px) {
  .banner {
    object-fit: contain;
    /* height: 200px; */
    margin-bottom: 20px;
    overflow: hidden;
  }
  .banner_contents {
    height: 50%;
    object-fit: contain;
    padding-bottom: 2rem;
  }
  .banner_title {
    font-size: 2rem;
    font-weight: 700;
    padding-bottom: 0.3rem;
  }
  .banner_description {
    object-fit: contain;
    font-style: 30px;
    text-align: left;
    justify-content: left;
    font-weight: 200;
    /* width:70% ; */
    line-height: 1;
    margin-right: 10px;
    padding-bottom: 2rem;
    margin-right: 30px;
    text-align: left;
    // overflow: hidden;
  }
}
